<template>
  <!--  -->
  <div
    class="card-img"
    id="card-img"
  >
    <!--  -->
    <card-type :card-type="cardType"></card-type>
    <!--  -->
    <card-title
      :card-type="cardType"
      :card-title="cardTitleFormate"
    ></card-title>
    <!--  -->
    <card-attribute :card-acctribute="cardAcctribute"></card-attribute>
    <!--  -->
    <card-star
      v-if="showStar"
      :number="cardNumber"
      :card-type="cardType"
    ></card-star>
    <!--  -->
    <card-right-attribute
      v-if="showRightAttribute"
      :right-attribute-type="rightAttributeType"
      :card-type="cardType"
    ></card-right-attribute>
    <!--  -->
    <card-inner-img :inner-img-src="innerImgSrc"></card-inner-img>
    <!--  -->
    <card-cardbag
      :cardbag-str="cardbagStr"
      :card-type="cardType"
    ></card-cardbag>
    <!--  -->
    <card-text
      :race-str="raceStr"
      :atk-str="atkStr"
      :def-str="defStr"
      :card-type="cardType"
      :content-html="contentHtmlFormate"
    ></card-text>
    <!-- id -->
    <card-id
      :card-id-str="cardIdStr"
      :card-type="cardType"
    ></card-id>
    <!--  -->
    <card-copyright
      :card-type="cardType"
      v-if="showCopyright"
    ></card-copyright>
  </div>
</template>

<script>
import CardType from "./CardType";
import CardTitle from "./CardTitle";
import CardAttribute from "./CardAttribute";
import CardStar from "./CardStar";
import CardRightAttribute from "./CardRightAttribute";
import CardInnerImg from "./CardInnerImg";
import CardCardbag from "./CardCardbag";
import CardText from "./CardText";
import CardId from "./CardId";
import CardCopyright from "./CardCopyright";

export default {
  name: "card-show",
  components: {
    CardType,
    CardTitle,
    CardAttribute,
    CardStar,
    CardRightAttribute,
    CardInnerImg,
    CardCardbag,
    CardText,
    CardId,
    CardCopyright,
  },
  computed: {},
  props: {
    cardTitle: {
      type: String,
      default() {
        return "error cardTitle";
      },
    },
    cardType: {
      type: String,
      default() {
        return "magic";
      },
    },
    cardAcctribute: {
      type: String,
      default() {
        return "light";
      },
    },
    cardNumber: {
      type: Number,
      default() {
        return 1;
      },
    },
    rightAttributeType: {
      type: String,
      default() {
        return "normal";
      },
    },
    innerImgSrc: {
      type: String,
      default() {
        return require("@/assets/img/inner.jpg");
      },
    },
    cardbagStr: {
      type: String,
      default() {
        return "error cardbagStr";
      },
    },
    raceStr: {
      type: String,
      default() {
        return "";
      },
    },
    atkStr: {
      type: String,
      default() {
        return "1000";
      },
    },
    defStr: {
      type: String,
      default() {
        return "1000";
      },
    },
    contentHtml: {
      type: String,
      default() {
        return "error contentHtml";
      },
    },
    cardIdStr: {
      type: String,
      default() {
        return "11111111";
      },
    },
    isBracketSubstitution: {
      type: Boolean,
      default() {
        return true;
      },
    },
    isS2t: {
      type: Boolean,
      default() {
        return false;
      },
    },
    showCopyright: {
      type: Boolean,
      default() {
        return true;
      },
    },
  },
  computed: {
    contentHtmlFormate() {
      let str = this.contentHtml;
      if (this.isS2t) {
        str = this.tran(str);
      }
      if (this.isBracketSubstitution) {
        str = str.replace(/\[/g, "").replace(/\]/g, "");
      }
      return str;
    },
    cardTitleFormate() {
      let str = this.cardTitle;
      if (this.isS2t) {
        str = this.tran(str);
      }
      if (this.isBracketSubstitution) {
        str = str.replace(/\[/g, "").replace(/\]/g, "");
      }
      return str;
    },
  },
  watch: {
    cardType(value) {
      switch (value) {
        case "magic":
        case "trap":
          this.showRightAttribute = true;
          this.showStar = false;
          break;
        default:
          this.showRightAttribute = false;
          this.showStar = true;
          break;
      }
    },
  },
  data() {
    return {
      showStar: true,
      showRightAttribute: false,
    };
  },
  async mounted() {},
  methods: {
    tran(str) {
      const s1 =
        "";
      const s2 =
        "";
      let a = "";
      for (let i = 0; i < str.length; i++) {
        const c = str.charAt(i);
        const p = s1.indexOf(c);
        a += p < 0 ? c : s2.charAt(p);
      }
      return a;
    },
  },
};
</script>
<style scoped>
.card-img {
  position: relative;
  width: 465px;
  height: 677px;
}
</style>